<template>
  <section class="pc-container pb-4">
    <div class="pcoded-content">
      <div class="row mb-4">
        <h5 class="text-dark fw-bold">Partes Procesales</h5>
      </div>
      <div class="row">
        <div class="col-xl-12">
          <div class="card shadow">
            <div class="card-header">
              <h5 class="text-secondary">Lista de Demandados</h5>
            </div>
            <div class="card-body">
              <div class="table-responsive">
                <table class="table" id="table" ref="myDataTable">
                  <thead>
                    <tr>
                      <th>Documento</th>
                      <th>Nombre / Razon Social</th>
                      <th>Telefono</th>
                      <th>Email</th>
                      <th class="text-center">Interacciones</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(persona, index) in personas" :key="index">
                      <td>{{ persona.documento }}</td>
                      <td>{{ persona.nombre }}</td>
                      <td>{{ persona.telefono }}</td>
                      <td>{{ persona.correo }}</td>
                      <td>
                        <div class="d-flex">
                          <div class="tooltip-container">
                            <router-link
                              :to="{ name: 'historial/registro-demandado', params: { doc: persona.documento } }"
                              class="tooltip-trigger">
                              <i data-feather="edit"></i>
                            </router-link>
                            <div class="custom-tooltip">Registrar historial</div>
                          </div>
                          <div class="tooltip-container">
                            <router-link :to="{ name: 'demandado/informacion', params: { doc: persona.documento } }"
                              class="tooltip-trigger">
                              <i data-feather="file-text"></i>
                            </router-link>
                            <div class="custom-tooltip">Datos del demandado</div>
                          </div>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import feather from 'feather-icons';
import DataTable from '../../utils/dataTable';

import { mapActions } from 'vuex';
export default {
  name: 'demandados',
  mixins: [DataTable],
  mounted() {
    this.leer();
  },
  updated() {
    feather.replace();
  },
  data() {
    return {
      demandados: [],
      dataTable: null,
    };
  },
  computed: {
    personas() {
      const personasMap = new Map();

      this.demandados.forEach((demandado) => {
        const key = demandado.tipo_persona === 'natural'
          ? demandado.nat_dni
          : demandado.jur_ruc;

        if (!personasMap.has(key)) {
          personasMap.set(key, {
            documento: key,
            nombre: demandado.tipo_persona === 'natural'
              ? `${demandado.nat_apellido_paterno} ${demandado.nat_apellido_materno} ${demandado.nat_nombres}`
              : demandado.jur_razon_social,
            direccion: demandado.dir_calle_av,
            telefono: demandado.tipo_persona === 'natural'
              ? demandado.nat_telefono
              : demandado.jur_telefono,
            correo: demandado.tipo_persona === 'natural'
              ? demandado.nat_correo
              : demandado.jur_correo,
            exp_id: demandado.exp_id,
          });
        }
      });

      return Array.from(personasMap.values());
    },
  },
  methods: {
    ...mapActions(['get', 'post']),
    async leer() {
      try {
        this.$showLoadingAlert();
        const result = await this.$getData('demandado', this);
        this.demandados = result.data;
        await this.$waitForDataTableDraw(this, 'myDataTable');
        this.$closeLoadingAlert();
      } catch (error) {
        this.$handleApiError(error, 'leer');
      }
    },
   
  },
  
};
</script>

<style scoped>
td {
  white-space: normal !important;
  word-wrap: break-word;
}

.circle {
  width: 30px;
  height: 30px;
  background-color: #7267EF;
  color: white;
  font-weight: 600;
  border-radius: 50%;
  text-align: center;
  line-height: 30px;
}
</style>
